import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';
import linkState from 'linkstate';

import Images from '../form/images';

import style from '../style.css';
import mu from '../../lib/musher-util';


const initialState = {
	story: {
        title: '',
        body: '',
        images: [],
        public: false,
    },
    showAddImage: false,
};

@observer
class StoryAdd extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.inputRef = {};
    }

    addStory = async () => {
        const { callback = () => {} } = this.props;
        const { story = {} } = this.state;
		const { storyStore } = this.props.stores;
		const response = await storyStore.insert({
            title: story.title,
            body: story.body,
            images: story.images,
            public: story.public ? 1 : 0,
            team: story.team,
        });
		if (response && response.status === 201) {
            await storyStore.loadPublicFeed();
            callback(response.data);
            this.setState({
                story: {
                    title: '',
                    body: '',
                    images: [],
                },
            }, () => {
                mu.resizeTextarea(this.inputRef['newStoryBody']);
            });
		}
	}

    addImages = ({ value }) => {
        // updateField({ id: object.id, field: 'images', value: images, skipTimer: true });
        const { story } = this.state;
        story.images.push(...value);
        this.setState({ story });
    }

    addImage = () => {
        const { showAddImage } = this.state;
        this.setState({
            showAddImage: !showAddImage,
        });
    }

    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    render() {
        const { col = 12, offset = 0 } = this.props;
        const { story, showAddImage } = this.state;
        const { userStore, appState } = this.props.stores;
        const { user = {} } = userStore;
        const displayNameShort = user.firstname ? `${util.ucfirst(user.firstname, true)}${util.ucfirst(user.lastname, true)}` : `${util.ucfirst(user.email || '', true)}`;
        // const displayName = user.firstname ? `${user.firstname} ${user.lastname}` : `${user.email}`;
        const userImage = user.image || (user.images ? user.images[0] : undefined);
        const { darkmode } = appState;

        return (
            <div class='w-100'>
                <div class='row bg-light'>

                    <div class={`col-${col} offset-${offset} col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 rounded rounded-lg pt-3 pb-1 bg-${darkmode ? 'black' : 'white'} shadow-sm mb-3 mt-0`}>
                        <div class='d-flex mb-0 position-relative w-100'>
                            <div 
                                class={`text-center border rounded-circle imageRounded imageRoundedSmall`}
                                style={`${userImage ? `background-image: url('${userImage.s3SmallLink}'); background-size: cover;` : ''} border-width: 3px !important;`}
                            >
                                {!user.image && <span class='font-weight-lighter' style='font-size: 15px;'>{displayNameShort}</span>}
                            </div>
                            <div class='col pl-3' style='line-height: 1.2em;'>

                                <div class='d-flex bd-highlight'>
                                    <div class='p-0 w-100 bd-highlight'>
                                        <label class='sr-only' for='inlineFormInput'><Text id='story.body'>Story</Text></label>
                                        <Localizer>
                                            <textarea
                                                class='form-control rounded'
                                                type='text'
                                                placeholder={<Text id='story.write-body'>Hva tenker du p?..</Text>}
                                                onInput={linkState(this, 'story.body')}
                                                onKeyUp={mu.resizeTextarea}
                                                style='height: 2.3em;'
                                                value={story.body}
                                                ref={c => this.inputRef['newStoryBody'] = c}
                                            />
                                        </Localizer>
                                    </div>
                                    <div class='p-0 pl-2 flex-shrink-1 bd-highlight'>
                                        <button type='button' class='btn btn-primary mb-2'
                                            onClick={this.addStory}
                                        ><i class='fas fa-paper-plane' /></button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div>
                            <div class='row'>
                                <div class='col-4 text-center'>
                                    <div class='form-group'>
                                        <small>
                                            <select
                                                class='custom-select custom-select-sm'
                                                id='team'
                                                onInput={linkState(this, 'story.team')}
                                            >
                                                {user.teams && user.teams.map((teamId) => {
                                                    const team = userStore.findTeam(teamId, true);
                                                    if (team && team.public) {
                                                        return (<option value={team.id}>{team.name}</option>);
                                                    }
                                                    return '';
                                                })}
                                            </select>
                                        </small>
                                    </div>
                                </div>
                                <div class='col-4 text-center'>
                                    <div class='form-group'>
                                        <div class='custom-control custom-control-sm custom-switch text-overflow'>
                                            <small>
                                                <input
                                                    type='checkbox'
                                                    class='custom-control-input'
                                                    id='public'
                                                    onInput={linkState(this, 'story.public')}
                                                    checked={story.public == 1}
                                                />
                                                <label class='custom-control-label' for='public'><Text id='story.public'>Is public</Text></label>
                                            </small>
                                        </div>
                                    </div>
                                </div>
                                <div class='col-4 text-center'>
                                    <small>
                                        <button type='button' class={`btn btn-sm`}
                                            data-id={story.id}
                                            onClick={this.addImage}
                                        >
                                            <i class={`text-success fas fa-image`} /> <Text id='store.add-image'>Add image</Text>
                                        </button>
                                    </small>
                                </div>
                                <div class='col-12 text-center'>
                                    {showAddImage && <>
                                        <div class='container-fluid'>
                                            <div class='row'>
                                                {story.images && story.images.map(img => (
                                                    <div class='col-3 p-1'>
                                                        <img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
                                                        <button class='btn btn-sm btn-link'
                                                            onClick={this.removeImage}
                                                            data-name={img.name}
                                                        ><i class='fas fa-trash' /> Del</button>
                                                    </div>
                                                ))}
                                            </div>
                                        </div>
                                        <Images object={story} updateField={this.addImages} />
                                    </>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default StoryAdd;
